/* GENERAL 
   ================================================= */

* { font-family: 'Press Start 2P', 'Times New Roman' }

.center { margin: 0 auto }
.text-center { text-align: center }
.block { display: block }
.i-block { display: inline-block }
.w-10 { width: 10% }
.w-15 { width: 15% }
.w-25 { width: 25% }
.w-30 { width: 30% }
.w-45 { width: 45% }
.w-50 { width: 50% }
.w-75 { width: 75% }
.w-95 { width: 95% }
.w-100 { width: 100% }
.mx-025 { margin: 0 .25em }
.mx-05 { margin: 0 .5em }
.mx-1 { margin: 0 1em }
.mt-05 { margin-top: .5em }
.mt-1 { margin-top: 1em }
.mb-1 { margin-bottom: 1em }
.font-sm { font-size: .6em }
.upper { text-transform: uppercase }
.cap { text-transform: capitalize }

.flex-center {
    align-items: center;
    display: flex;
    justify-content: center;
}

.separator { border-bottom: 1px solid #F0F0F0 }

.box-rounded {
    border-radius: 8px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.box-md {
    height: 2em;
    line-height: 2em;
}

/* Objects */

.banner {
    color: white;
    height: 7.5%;
    margin-bottom: 2.5%;
}

.search {
    height: 3em;
    line-height: 3em;
}

.sprite {
    margin-right: 1em;
    max-height: 96px;
    max-width: 96px;
}

/* Team List */

.poke-name {
    width: calc(100% - (96px + 5em)); /* Fill the space left by sprite and buttons */
}

.poke-move {
    margin-left: 1em;
    width: 1.5em;
}

/* Card Grid */

.card {
    border-radius: 8px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 4fr 1fr 5fr;
    grid-template-areas: 
        "pokemon moves"
        "modifiers modifiers"
        "stats stats";
    height: 35%;
    grid-row-gap: .5em;
    row-gap: .5em;
}

.p-name {
    -webkit-margin-before: .75em;
            margin-block-start: .75em;
    -webkit-margin-after: .25em;
            margin-block-end: .25em;
}

.card-poke { grid-area: pokemon }
.card-moves { grid-area: moves }
.card-modifiers { grid-area: modifiers }
.card-stats { grid-area: stats }

/* ANIMATIONS
   ================================================= */

@-webkit-keyframes shake {
    10%, 90% { -webkit-transform: translate3d(-1px, 0, 0); transform: translate3d(-1px, 0, 0) }
    20%, 80% { -webkit-transform: translate3d(2px, 0, 0); transform: translate3d(2px, 0, 0) }
    30%, 50%, 70% { -webkit-transform: translate3d(-4px, 0, 0); transform: translate3d(-4px, 0, 0) }
    40%, 60% { -webkit-transform: translate3d(4px, 0, 0); transform: translate3d(4px, 0, 0) }
}

@keyframes shake {
    10%, 90% { -webkit-transform: translate3d(-1px, 0, 0); transform: translate3d(-1px, 0, 0) }
    20%, 80% { -webkit-transform: translate3d(2px, 0, 0); transform: translate3d(2px, 0, 0) }
    30%, 50%, 70% { -webkit-transform: translate3d(-4px, 0, 0); transform: translate3d(-4px, 0, 0) }
    40%, 60% { -webkit-transform: translate3d(4px, 0, 0); transform: translate3d(4px, 0, 0) }
}

.error { -webkit-animation: shake 1s linear; animation: shake 1s linear }
